import React from 'react';
import {render} from 'react-dom';

//CSS
import './style/css/bootstrap.min.css'; 
import './index.css'; 

//DRAFT JS
import { EditorState,  convertToRaw, convertFromRaw  } from 'draft-js';

import Editor from 'draft-js-plugins-editor';
import createHashtagPlugin from 'draft-js-hashtag-plugin';

//mention plugin
import createMentionPlugin, { defaultSuggestionsFilter } from 'draft-js-mention-plugin'; // eslint-disable-line import/no-unresolved
import editorStyles from './style/css/editorStyles.css';
import mentions from './mentions';
//DRAFT CSS PLUGINS
import 'draft-js-mention-plugin/lib/plugin.css';
import 'draft-js-hashtag-plugin/lib/plugin.css'; // eslint-disable-line import/no-unresolved



const hashtagPlugin = createHashtagPlugin();
const mentionPlugin = createMentionPlugin();
const { MentionSuggestions } = mentionPlugin;

const plugins = [
  hashtagPlugin,
  mentionPlugin
];



 

class App extends React.Component{
	
 	constructor(props) {
	    super(props);
	    const rawContent = JSON.parse(localStorage.getItem('text'));
	     console.log( rawContent);
	    if(rawContent != null){
			this.state = {
				//editorState: EditorState.createEmpty(),
		     	editorState: EditorState.createWithContent(convertFromRaw(rawContent)) ,
				suggestions: mentions,
		    }
	    }else {
	    	this.state = {
		     	editorState: EditorState.createEmpty(),
				suggestions: mentions,
		    }
	    }
	     
	    
	 }

	 

	onChange = (editorState) => {
		this.setState({
		  editorState,
		});
		
		localStorage.setItem('text', 
			JSON.stringify( convertToRaw(editorState.getCurrentContent()) )
		);
		// console.log(this.state.editorState, editorState);
	};


	onSearchChange = ({ value }) => {
	this.setState({
	  suggestions: defaultSuggestionsFilter(value, mentions),
	});
	};

	onAddMention = () => {
	// get the mention object selected
	}

	focus = () => {
	this.editor.focus();
	};
	
	render() {
	return (
		<div className="container">
		<h1>Draft editor</h1>
	 <div className={editorStyles.editor} onClick={this.focus}>
	    <Editor
	      editorState={this.state.editorState}
	      onChange={this.onChange}
	      plugins={plugins}
	      ref={(element) => { this.editor = element; }}
	    />
	    <MentionSuggestions
	      onSearchChange={this.onSearchChange}
	      suggestions={this.state.suggestions}
	      onAddMention={this.onAddMention}
	    />
	  </div>
	  </div>
	);
	}

}

render(
	<App />,
	document.getElementById('root')
);


